export default {
	data() {
		return {
			message: '',
			fileList: '',
			historyShow: true,
			// historyList 用来接收输入框的历史数据
			historyList: []

		}
	},
	onLoad() {
		// 页面一进去 就获取本地储存数据
		// uni.getStorage({
		// 	key: 'hList',
		// 	// success: function(res) {
		// 	// 	// this.historyList = res.data
		// 	// 	
		// 	// }
		// });
	},
	watch: {
		message(newVal) {
			console.log(newVal);
			if (newVal !== '') {
				this.historyShow = false
				uni.request({
					url: 'http://192.168.31.9/api/files/Searchfile',
					method: 'POST',
					dataType: "json",
					data: {
						"path": this.message
					},
					success: (res) => {
						this.fileList = res.data.result
					},
					header: {
						"Cookie": "user_id=1; login_status=ok; login_key=6755d4bc360597c18784e950350b0c13"
					},
				})
			} else {
				this.fileList = ''
				this.historyShow = true
			}
		}
	},
	methods: {
		del() {
			this.message = ''
		},
		searchNow() {

			let arr = this.historyList
			arr.unshift(this.message)
			this.historyList = arr
			console.log(this.historyList);

			uni.getStorage({
				key: 'hList',
				success: function(res) {
					// 						console.log(arr.indexOf(this.message));

					if (arr.length > 4) {
						arr.pop()
					}
				}
			});
			// 先把值存到本地
			// 再次输入的时候 判断是否跟本地的值一样 如果不一样 就添加 如果一样就不添加
			// 当记录大于五条的时候 从前面删除
			uni.setStorage({
				key: 'hList',
				data: arr,
			});
			// this.message =''
		},
		clearAll() {
			// this.message = ''
			if (this.historyList.length > 0) {
				uni.showModal({
					showCancel: true,
					title: '删除全部',
					content: '确定清空历史记录吗',
					success: (result) => {
						// console.log("confirm = " + result.confirm);
						if (result.confirm) {
							this.historyList = []
							this.message = ''
							uni.removeStorage({
								key: 'hList',
							});
						}
					}
				})
			}

		},
		clearOne(e) {
			console.log(e);
			uni.getStorage({
				key: 'hList',
				success: function(res) {
					// this.historyList = res.data
					// console.log(res.data);
					// // this.historyList.remove(e)
					// console.log(this.historyList);

				}
			});
		}

	}
}
